import React, { useEffect, useState, useRef } from 'react';
import { ActionIcon } from '@mantine/core';
import GraphIcon from './icons/GraphIcon';

import { useSelector, useDispatch } from 'react-redux'
import { toggleGraphs } from '../reducers/graphSlice'
import { resetSpin } from '../reducers/locationSlice'

const buttonStyle = {
  width: "60px",
  height: "60px",
  padding: "10px",
  margin: "0px",
  '--ai-bg': 'rgb(0, 0, 0, 0.2)',
  '--ai-hover': 'rgb(255, 255, 255, 0.3)',
  pointerEvents: "auto",
  borderColor: "#94133377",
  borderWidth: "2px",
}

function ToggleGraphs() {
  const displayGraphs = useSelector(state => state.graph.graphsVisible)
  const doSpin = useSelector(state => state.location.doSpin)
  const dispatch = useDispatch()

  let className = displayGraphs ? 'buttonActive' : ''
  className = doSpin ? `spinButton ${className}` : className;
  console.log(`ToggleGraphs: ${className}`);

  // if (doSpin) {
  //   dispatch(resetSpin())
  // }

  return (
    <>
      <ActionIcon 
        // key={className}
        variant="filled"
        style={buttonStyle}
        className={className}
        onClick={() => dispatch(toggleGraphs())}
        onAnimationEnd={() => dispatch(resetSpin())}
        styles={{
          root: {
            padding: '0',
            borderRadius: '100px',
          }
        }}
      >
        <GraphIcon />
      </ActionIcon>
    </>
  );
}

export default ToggleGraphs;
