import React, { useEffect, useState } from 'react';
import { Title, Center, Paper, Stack, ScrollArea, Badge, Box } from '@mantine/core';
import Plot from "react-plotly.js";
import scrollBarClass from './ScrollBar.module.css';
import { useSelector } from 'react-redux';

export default function MakeGraph({ width, height, position, variable }) {
    function getDecimalPart(number) {
        // Just while testing with random data
        return number % 1;
    }

    function generateRandomArray(min: number, max: number, length: number) {
        let randomArray: number[] = [];
        for (let i = 0; i < length; i++) {
            const randomNumber = Math.floor(Math.random() * (max - min + 1)) + min;
            randomArray.push(randomNumber);
        }
        return randomArray;
    }
    

    function getData(position, variable) {
        let yvalsOrdered
        switch (variable) {
            case "Rainfall annual timeseries":
            case "Temperature annual timeseries":
            case "Temperature warming stripes":
                yvalsOrdered = generateRandomArray(20, 40, 84);
                break
            default:
                yvalsOrdered = [32, 37, 40.5, 43, 49, 54, 59, 63.5, 69.5, 73, 74];
                break
        }
        const yvalsRandom = shuffleArray(yvalsOrdered);
        return yvalsRandom;
    }

    function shuffleArray(array) {
        for (let i = array.length - 1; i > 0; i--) {
            // Pick a random index from 0 to i
            const j = Math.floor(Math.random() * (i + 1));
            // Swap elements array[i] and array[j]
            [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
    }


    // const position2 = useSelector(state => state.location.locationPosition);
    // const [position3, setPos] = useState(position2);
    const yvals = getData(position, variable)

    console.log('MakeGraphs', position)

    let seasonal_strings = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]

    let startYear = 1940;
    let endYear = 2023;
    let annual_values = Array.from({ length: endYear - startYear + 1 }, (v, k) => k + startYear);

    let markerColor, plotType, xvals, plotMode
    switch (variable) {
        case "Rainfall annual climatology":
            markerColor = "#275eb0";
            plotType = "bar"
            plotMode = "markers"
            xvals = seasonal_strings
            break
        case "Temperature annual climatology":
            markerColor = "#941333";
            plotType = "bar"
            plotMode = "markers"
            xvals = seasonal_strings
            break
        case "Rainfall annual timeseries":
            markerColor = "#275eb0";
            plotType = "scatter"
            plotMode = "lines+markers"
            xvals = annual_values
            break
        case "Temperature annual timeseries":
            markerColor = "#941333";
            plotType = "scatter"
            plotMode = "lines+markers"
            xvals = annual_values
            break
        case "Temperature warming stripes":
            markerColor = "#941333";
            plotType = "bar"
            xvals = annual_values
            break
        case "Wind-speed annual climatology":
            markerColor = "#0b730f";
            plotType = "bar"
            xvals = seasonal_strings
            break
        default:
            markerColor = "#000000";
            plotType = "bar"
            xvals = seasonal_strings
            break
    }

    return (
        <Plot
            data={[
                {
                    x: xvals,
                    y: yvals,
                    mode: plotMode,
                    type: plotType,
                    name: "A",
                    marker: { color: markerColor },
                },
                // {
                //     x: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
                //     y: [30, 36, 39, 42, 48, 53, 58, 62, 67.5, 68, 68.5],
                //     mode: "lines+markers",
                //     type: "bar",
                //     name: "B",
                //     marker: { color: "#941333" },
                // },
            ]}
            layout={{
                xaxis: {
                    tickangle: -45,
                    showgrid: false,
                    fixedrange: true,
                },
                yaxis: {
                    showgrid: false,
                    fixedrange: true,
                },
                width: `${width}`,
                height: `${height}`,
                // pad: {
                //     b: 0,
                //     l: 0,
                //     r: 0,
                //     t: 0,
                // },
                showlegend: false,
                margin: {
                    b: 30,
                    l: 0,
                    r: 0,
                    t: 0,
                    // autoexpand: true,
                },
                paper_bgcolor: 'transparent',
                plot_bgcolor: 'transparent',
            }}
            config={{
                // staticPlot: true,
                displayModeBar: false,
            }}
        />
    )
}