const mercatorTiles = "https://{s}.tile.osm.org/{z}/{x}/{y}.png"

const mapStyle = {
  position: 'fixed',
  left: '0px',
  right: '0px',
  top: '0px',
  bottom: '0px',
}

const c3sred = '#941333'

